import { getStudents, getStudentStats } from "@/actions/user-students";
import {
    getEnrollmentTrend,
    getProgramDistribution,
} from "@/actions/user-students-charts";
import { SubNavigation } from "@/components/sub-navigation";
import { StatsCard } from "@/components/dashboard/stats-card";
import { ChartCard } from "@/components/dashboard/chart-card";
import { QuickActions } from "@/components/dashboard/quick-actions";
import { EnrollmentTrendChart } from "@/components/dashboard/charts/enrollment-trend-chart";
import { ProgramDistributionChart } from "@/components/dashboard/charts/program-distribution-chart";
import {
    Users,
    UserCheck,
    TrendingUp,
    GraduationCap,
    Plus,
    Upload,
    FileText,
    AlertCircle,
} from "lucide-react";

const subNavItems = [
    { label: "Overview", href: "/admin/users/students" },
    { label: "Active Students", href: "/admin/users/students/active" },
    { label: "On Leave", href: "/admin/users/students/on-leave" },
    { label: "Reports", href: "/admin/users/students/reports" },
];

export default async function StudentsOverviewPage() {
    const [statsResult, enrollmentTrendResult, programDistResult] = await Promise.all([
        getStudentStats(),
        getEnrollmentTrend(),
        getProgramDistribution(),
    ]);

    const stats = statsResult.success
        ? statsResult.stats
        : {
            total: 0,
            studying: 0,
            graduated: 0,
            onLeave: 0,
        };

    const enrollmentData = enrollmentTrendResult.success ? enrollmentTrendResult.data : [];
    const programData = programDistResult.success ? programDistResult.data : [];

    // Calculate current students (excluding graduated)
    const currentStudentsTotal = (stats?.studying ?? 0) + (stats?.onLeave ?? 0);

    return (
        <div className="flex h-full flex-col">
            <SubNavigation items={subNavItems} />
            <div className="flex-1 space-y-6 p-8">
                {/* Header */}
                <div>
                    <h1 className="text-3xl font-bold tracking-tight">Students Overview</h1>
                    <p className="text-muted-foreground">
                        Dashboard for managing current students and tracking enrollment
                    </p>
                </div>

                {/* Stats Grid */}
                <div className="grid gap-4 md:grid-cols-4">
                    <StatsCard
                        title="Current Students"
                        value={currentStudentsTotal}
                        icon={Users}
                        color="blue"
                        description="Active + On Leave"
                    />
                    <StatsCard
                        title="Studying"
                        value={stats?.studying ?? 0}
                        icon={UserCheck}
                        color="green"
                        description="Currently enrolled"
                    />
                    <StatsCard
                        title="On Leave"
                        value={stats?.onLeave ?? 0}
                        icon={TrendingUp}
                        color="orange"
                        description="Temporarily absent"
                    />
                    <StatsCard
                        title="Total (All Time)"
                        value={stats?.total ?? 0}
                        icon={GraduationCap}
                        color="purple"
                        description="Including graduated"
                    />
                </div>

                {/* Charts Row */}
                <div className="grid gap-6 md:grid-cols-2">
                    <ChartCard
                        title="Enrollment Trends"
                        description="New student enrollments over the last 6 months"
                    >
                        {enrollmentData.length > 0 ? (
                            <EnrollmentTrendChart data={enrollmentData} />
                        ) : (
                            <div className="flex items-center justify-center h-64 text-muted-foreground">
                                <p className="text-sm">No enrollment data available</p>
                            </div>
                        )}
                    </ChartCard>

                    <ChartCard
                        title="Program Distribution"
                        description="Current students by academic program"
                    >
                        {programData.length > 0 ? (
                            <ProgramDistributionChart data={programData} />
                        ) : (
                            <div className="flex items-center justify-center h-64 text-muted-foreground">
                                <p className="text-sm">No program data available</p>
                            </div>
                        )}
                    </ChartCard>
                </div>

                {/* Quick Actions */}
                <QuickActions
                    actions={[
                        {
                            label: "Add Student",
                            href: "/admin/users/students/create",
                            icon: Plus,
                            variant: "primary",
                        },
                        {
                            label: "View Active Students",
                            href: "/admin/users/students/active",
                            icon: Users,
                            variant: "outline",
                        },
                        {
                            label: "Import Students",
                            href: "#",
                            icon: Upload,
                            variant: "outline",
                        },
                        {
                            label: "Generate Report",
                            href: "/admin/users/students/reports",
                            icon: FileText,
                            variant: "outline",
                        },
                    ]}
                />

                {/* Reports Summary & Quick Links */}
                <div className="grid gap-6 md:grid-cols-3">
                    {/* Academic Performance Summary */}
                    <div className="rounded-lg border bg-card p-6">
                        <h3 className="text-lg font-semibold mb-4 flex items-center gap-2">
                            <GraduationCap className="h-5 w-5 text-blue-600" />
                            Academic Summary
                        </h3>
                        <div className="space-y-3 text-sm">
                            <div className="flex justify-between">
                                <span className="text-muted-foreground">Current Students:</span>
                                <span className="font-semibold">{currentStudentsTotal}</span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-muted-foreground">Graduated:</span>
                                <span className="font-semibold">{stats?.graduated ?? 0}</span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-muted-foreground">Completion Rate:</span>
                                <span className="font-semibold text-green-600">
                                    {stats?.total && stats.total > 0
                                        ? Math.round(
                                            ((stats.graduated ?? 0) / stats.total) * 100
                                        )
                                        : 0}
                                    %
                                </span>
                            </div>
                        </div>
                    </div>

                    {/* Status Overview */}
                    <div className="rounded-lg border bg-card p-6">
                        <h3 className="text-lg font-semibold mb-4 flex items-center gap-2">
                            <AlertCircle className="h-5 w-5 text-orange-600" />
                            Status Overview
                        </h3>
                        <div className="space-y-3 text-sm">
                            <div className="flex justify-between">
                                <span className="text-muted-foreground">Active:</span>
                                <span className="font-semibold text-green-600">
                                    {stats?.studying ?? 0}
                                </span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-muted-foreground">On Leave:</span>
                                <span className="font-semibold text-orange-600">
                                    {stats?.onLeave ?? 0}
                                </span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-muted-foreground">Leave Rate:</span>
                                <span className="font-semibold">
                                    {currentStudentsTotal > 0
                                        ? Math.round(
                                            ((stats.onLeave ?? 0) / currentStudentsTotal) * 100
                                        )
                                        : 0}
                                    %
                                </span>
                            </div>
                        </div>
                    </div>

                    {/* Quick Links */}
                    <div className="rounded-lg border bg-card p-6">
                        <h3 className="text-lg font-semibold mb-4">Quick Links</h3>
                        <div className="space-y-2">
                            <a
                                href="/admin/users/students/active"
                                className="block text-sm text-blue-600 hover:underline"
                            >
                                → View All Active Students
                            </a>
                            <a
                                href="/admin/users/students/on-leave"
                                className="block text-sm text-blue-600 hover:underline"
                            >
                                → Students On Leave
                            </a>
                            <a
                                href="/admin/users/alumni"
                                className="block text-sm text-blue-600 hover:underline"
                            >
                                → View Alumni (Graduated)
                            </a>
                            <a
                                href="/admin/academic/groups"
                                className="block text-sm text-blue-600 hover:underline"
                            >
                                → Manage Student Groups
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
